
.team-box {
    &:hover {
        .item-img {
            a {
                 img {
                    filter: grayscale(100%);
                    transform: scale(1.05);
                }
            }
        }
    }
    .item-img {
        border-radius: 20px;
        z-index: 1;
        a {
            img {
                border-radius: 20px;
                transition: all .35s ease;
            }
        }
        &::after {
            content: "";
            height: 20%;
            width: 100%;
            background-color: #000;
            filter: blur(70px);
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }
}

.team-content {
    position: absolute;
    bottom: 25px;
    left: 30px;
    right: 30px;
    z-index: 1;
    .content {
        .name {
            font-size: 24px;
            font-weight: 600;
            line-height: normal;
            text-transform: capitalize;
            a {
                color: $color-white;
            }
        }
        .designation {
            color: #F1F1F1;
            font-size: 18px;
            font-weight: 400;
        }
    }
}

.team-social {
    position: relative;
    cursor: pointer;
    &:hover {
        .item-social {
            ul {
                li {
                    transform: translateY(0);
                    opacity: 1;
                    backface-visibility: visible;
                }
            }
        }
    }
    .icon-box {
        border-radius: 50px;
        background: rgba(0, 0, 0, 0.25);
        display: inline-block;
        text-align: center;
        border-radius: 50px;
        height: 50px;
        width: 50px;
        line-height: 50px;
        svg {
            color: $color-white;
        }
    }
    .item-social {
        position: absolute;
        bottom: 55px;
        right: 5px;
        ul {
            li {
                opacity: 0;
                backface-visibility: hidden;
                transform: translateY(10px);
                transition: all 0.3s ease-in-out;
                line-height: 45px;
                &:nth-child(1) {
                    transition-delay: .4s;
                }
                &:nth-child(2) {
                    transition-delay: .3s;
                }
                &:nth-child(3) {
                    transition-delay: .2s;
                }
                &:nth-child(4) {
                    transition-delay: .1s;
                }
                &:last-child {
                    a {
                        margin-bottom: 0;
                    }
                }
                a {
                    background-color: #fff;
                    margin-bottom: 1px;
                    transition: all .3s ease-in-out;
                    border: 0;
                    &.item-fb {
                        background-color: #E7F1FF;
                        color:#1877F2;
                        i {
                            color:#1877F2;
                        }
                        &:hover {
                            background-color:#1877F2;
                            border-color: #1877F2;
                            i {
                                color: $color-white;
                            }
                        }
                    }
                    &.item-twit {
                        i {
                            color: #1DA1F2;
                        }
                        &:hover {
                            background-color:#1DA1F2;
                            border-color: #1DA1F2;
                            i {
                                color: $color-white;
                            }
                        }
                    }
                    &.item-In {
                        background-color: #E1F5FF;
                        color:#0077B5;
                        i {
                            color:#0077B5;
                        }
                        &:hover {
                            background-color:#0077B5;
                            border-color: #0077B5;
                            i {
                                color: $color-white;
                            }
                        }
                    }
                    &.item-insta {
                        color: #E60023;
                        background-color: #FFEBEE;
                        i {
                            color: #FD1D1D;
                        }
                        &:hover {
                            background-color:#FD1D1D;
                            border-color: #FD1D1D;
                            i {
                                color: $color-white;
                            }
                        }
                    }
                }
            }
        }
    }
}

.single-team-wrap {
    padding: 80px 0 45px;
    @include media-only ("lg", min) {
        padding: 120px 0 85px;
    }
}

.single-team-box {
    .phone-mail-wrapper {
        margin-bottom: 20px;
        .phone-wrap {
            margin-bottom: 10px;
            .text {
                font-weight: 700;
                font-size: 18px;
                line-height: 30px;
                color: $color-black;
            }
            a {
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                transition: all .4s ease-in-out;
                color: $color-black;
                &:hover {
                    color: $color-primary;
                }
            }
        }
        .mail-wrap {
            margin-bottom: 0;
        }
    }
    .description {
        margin-bottom: 30px;
    }
}

.team-widget {
    .content {
        padding-top: 20px;
        .title {
            text-align: center;
            margin-bottom: 5px;
            font-size: 24px;
            transition: all .4s ease-in-out;
            font-weight: 600;
        }
        .designation {
            font-size: 16px;
            text-align: center;
            text-transform: capitalize;
        }
        .item-social {
            margin-top: 12px;
            ul {
                justify-content: center;
            }
        }
    }
}

.single-skill-wrapper {
    .single-skill {
        margin-bottom: 28px;
        padding-bottom: 5px;
        position: relative; 
        // &::before {
        //     content: "";
        //     height: 1px;
        //     width: 100%;
        //     background-color: #d1d1d1;
        //     position: absolute;
        //     bottom: 0;
        //     left: 0;
        //     border-radius: 20px;
        // }
        .title-bar {
            .title {
                font-weight: 600;
                font-size: 20px;
                line-height: 25px;
                margin-bottom: 17px;
                color: var(--color-black);
            }
        }
        .skill-bar {
            background-color: #d9d9d9;
            border-radius: 10px;
            height: 5px;
            padding: 0;
            border: 0;
            .skill-per {
                height: 5px;
                width: 0;
                background: $color-primary;
                border-radius: 10px;
                transition: 2s linear;
                position: relative;
                &::before {
                    content: attr(data-per);
                    position: absolute;
                    font-weight: 600;
                    font-size: 15px;
                    line-height: 20px;
                    color: $color-black;
                    bottom: 20px;
                    right: 0;
                    font-family: var(--font-title);
                }
            }
        }
    }
}